export default {"name":"苏州茉莉花茶","img":"\\zaijiagongcha\\shuzhoumolihuacha\\img_20191122133121evyvpu.jpg","desc":["茉莉花茶中的佳品，中国十大名茶之一。据史料记载，苏州在宋代时已栽种茉莉花，并以它作为制茶的原料。用作为，精选茉莉花，通过传统的窨制工艺，把茉莉花的香气融合到茶香中，香氛迷人，“窨得茉莉无上味，列作人间第一香。”如此天堂香味，倾倒无数茶人。"],"menus":[{"name":"苏州茉莉花茶茶叶特点","content":[{"text":"苏州茉莉\n        花茶\n        外观条索紧细匀整，白毫显露，干茶色泽油润;冲泡后的茶汤清澈透明，叶底幼嫩。香气鲜美、浓厚、清高，入口爽快，持续性能好。"},{"src":"\\zaijiagongcha\\shuzhoumolihuacha\\img_20191122133121qlw04t.jpg"}]},{"name":"苏州茉莉花茶加工工艺","content":[{"text":"烘青茶坯 茉莉\n        花茶\n        一般采用烘青\n        绿茶\n        作茶坯，不但因为烘青绿茶的组织结构疏松，吸香性能好，而且因其香、味清纯，与茉莉花的香型较协调，窨制的成品，茶味与花香融和无间，因此品赏时，有清新鲜爽的愉快感受。烘青茶坯制作如烘青绿茶，其茶青采用幼嫩的芽叶，一般也分为初制与精制两个过程。初制的成品称为“毛茶”，经过整形和分级后的成品称为“精茶”，供窨制花茶的精茶即为“茶坯”。"},{"text":"质量好的毛茶是制成优质茶坯的基础，优质的茶坯才能窨出优质的茉莉花茶。茶坯的初制过程主要有：萎凋、杀青、揉捻、烘焙。造型工艺茉莉花茶的茶坯采用造型工艺绿茶，其制作过程主要为：萎凋、杀青、烘焙(七成干)、手工造型、烘干。茶坯制成后便可匀堆装箱，仓储待窨。"},{"text":"茉莉花茶的窨制 其基本工艺流程:茶坯处理→鲜花养护→拌和窨花→通花散热→收堆续窨→出花分离→湿坯复火干燥→再窨或提花。"},{"src":"\\zaijiagongcha\\shuzhoumolihuacha\\img_20191122133121oih2ic.jpg"},{"text":"从茶花拌和到烘焙，称为一个窨次。生产上根据不同的质量标准，采用不同的窨次。高级茉莉花茶用花量多，需要较多的窨次。转窨是指每一窨次茶叶烘后，不进行匀堆装箱，而待茶叶降温后，达到技术要求时再复窨花。连窨是指两窨之间未经烘焙直接转窨下一个窨次。"},{"text":"1、茶坯处理：对茶坯进行复火干燥后再进行通凉降温，主要目的是提高茶香和控制茶坯的含水量与坯温，以适应窨花工艺的技术要求。"},{"text":"2、鲜花养护：分为“伺花”和“筛花”两个环节。伺花是指交替进行鲜花摊凉散热和复堆升温，并结合适挡的的翻动，促使花蕾在一定温度和充分供氧的条件下，开放均匀。筛花是指去除杂物，筛选净花，并进行分级，以便按级对茶配花，同时起振荡作用，进一步促进鲜花开放。"},{"text":"4、通花散热：静置一定时间后，随着堆温的升高需进行通花工序。通花即将茶叶翻动摊开，温度下降到一定程度后再收堆续窨。通花起到散发热量，置换堆内空气及再一次的茶花拌和的作用。"},{"text":"5、起花：是将已窨过茶的花渣筛出，使茶花由混合到分离。"},{"text":"6、烘焙：由于窨后茶叶含水量增加，必须进行烘焙，使茶叶具有一定干度，以利于在窨品转窨或成品品质，同时促进茶香和花香的更好结合。"},{"text":"7、提花：经过烘焙的茶叶，花香鲜灵不足，工艺上采取在最后一窨次以少量优质的茉莉鲜花与茶叶拌和后静置数小时，未经通花，起花后不经烘焙即行匀堆装箱。"}]},{"name":"苏州茉莉花茶储藏方法","content":[{"text":"1、罐藏法"},{"text":"容器选用装糕点或者其他食品的金属听、箱、罐、盒，或铁或铝或纸或纸品，或方或圆或扁或不规则性。重要的是茶要干燥，袋口封好。此法简便，取同方便。"},{"text":"2、塑料袋贮茶法"},{"text":"选用密度高、高压、厚实、强度好、无异味的食品包装袋。茶叶可以事先用较柔软的净纸包好，然后置于食品袋内，封口即成。"},{"text":"3、热水瓶贮茶法"},{"text":"可用保温不佳而废弃的热水瓶，内充干燥的茉莉\n        花茶\n        ，盖好瓶塞，用蜡封口。"},{"text":"4、冰箱保存法"},{"text":"茉莉花茶装入密度高、高压、厚实、强度好、无异味的食品包装袋，然后置于冰箱冷冻室或者冷藏室。此法保存时间长、效果好，但袋口一定封牢，封严实，否则会回潮或者串味，反而有损茉莉花茶茶叶的品质。"}]},{"name":"苏州茉莉花茶冲泡方法","content":[{"text":"冲泡方法：采用碗盅泡法 。"},{"text":"投茶法：下投法 茶水比例:1∶20 。"},{"src":"\\zaijiagongcha\\shuzhoumolihuacha\\img_201911221331215wbuvd.jpg"},{"text":"泡茶时间：一泡2~3分钟，冲泡水温是100℃。"},{"text":"1 备器：准备盖碗等\n        茶具\n        ，同时择水备用。"},{"text":"2 赏茶：欣赏茉莉\n        花茶\n        的外形和色泽。"},{"text":"3 温碗：向盖碗中注入100℃的热水温碗。"},{"text":"4 温盅：将温盖碗的水倒入茶盅内温盅。"},{"text":"5 温杯：将茶盅内的水分斟入品茗杯中，温杯后直接弃入水盂。"},{"text":"6 投茶：用茶匙将茶荷中的干茶拨入盖碗中。"},{"text":"7 注水：高冲注水入盖碗中，水量至碗沿即可。"},{"text":"8 出汤：约2~3分钟后，将盖碗内茶汤倒入茶盅中。"},{"text":"9 分斟：将茶盅内茶汤分斟入品茗杯，即可品饮。"}]},{"name":"苏州茉莉花茶功效作用","content":[{"text":"1、减肥降脂"},{"text":"增强心脏血管可见冠心病、心肌梗、脑溢血和脑血栓等疾病对人类的健康构成了严重的威胁。\n        花茶\n        中含有的茶色素、维生素C等具有减少组织胆固醇沉积，增加纤维蛋白溶解活性和减少纤维蛋白原的浓度，同时还有扩张周围血管、防止血栓形成。有助于防止血管阻塞性病变和病情的发展。也可作用于心肌梗塞后预防复发和预防动脉血栓栓塞。对于中老年人饮用花茶极有好处，能有效地保护心脏，预防心血管病。每天能坚持饮上花茶，养成一种良好习惯、可使原有心脏病发作的危险因素降低约45%。"},{"text":"2、除疲劳解油腻"},{"text":"人们在工作、学习中、也常会出现疲劳现象，总感到脑力和体力不足，工作效率低、记忆力减退或身体不适和睡眠障碍等。茶叶中的咖啡碱则有兴奋大脑，消除紧张和焦虑，并能促进新陈代谢。饮上一杯茉莉花茶，振奋精神，使大脑思维活动迅速敏捷，感觉敏锐，记忆力集中，办事工作效率增加。"},{"text":"3、帮助消化"},{"text":"随着生活水平的不为断提高，食物结构已经发生了变化，肉食比例增加了。胃肠吸入脂肪明显增多，总感到胃油腻、饱胀感。花茶中的咖啡碱、黄烷醇类化合物增强胃、肠蠕动。能剌激胃液分泌(主要是胃酸、胃蛋白酶、内因子和粘液)其作用是将无活性的胃蛋白酶原激活成有活性的胃蛋白酶，为胃蛋白酶发挥作用提供酸性环境;使食物中蛋白质变性易于分解;杀死随食物进入胃内的细菌;促进食物消化增加氧化分解脂肪的能力，消除食欲不振、腹胀和消化不良。饮茶后常感到胃肠‘舒服感’。"}]},{"name":"苏州茉莉花茶历史故事","content":[{"text":"传说是在很早以前北京茶商陈古秋所创制，陈古秋为什么想出把茉莉花加到茶叶中去呢，其中还有个小故事。有一年冬天，陈古秋邀来一位品茶大师，研究北方人喜欢喝什么茶，正在品茶评论之时，陈古秋忽然想起有位南方姑娘曾送给他一包茶叶未品尝过，便寻出那包茶，请大师品尝。冲泡时，碗盖一打开，先是异香扑鼻，接着在冉冉升起的热气中，看见有一位美貌姑娘，两手捧着一束茉莉花，一会功夫又变成了一团热气。陈古秋不解就问大师，大师笑着说:“陈老弟，你这是好事啦，这乃茶中绝品‘报恩仙’，过去只听说过，今日才亲眼所见，这茶是谁送你的”。陈古秋就讲述了他去南方购茶时在住客店遇见一位孤苦伶仃少女，那少女诉说家中停放着父亲尸身，无钱殡葬，陈古秋深为同情，便取了一些银子给她，并请邻居帮助她搬到亲戚家去。 今春又去南方时，客店“茉莉\n        花茶\n        ”的老板转交给他这一小包茶叶，说是那位少女交送的。当时未冲泡，谁料是珍品。"},{"text":"大师说:“这茶是珍品，是绝品，制这种茶要耗尽人的精力，这姑娘可能你再也见不到了。”陈古秋说当时问过客店老板，老板说那姑娘已死去一年多了。两人感叹一会，大师忽然说: “为什么她独独捧着茉莉花呢?” 两人又重复冲泡了一遍，那手捧茉莉花的姑娘又再次出现。陈古秋一边品茶一边悟道:“依我之见，这是茶仙提示，茉莉花可以入茶。”次年便将茉莉花加到茶中，果然制出了芬芳诱人的茉莉花茶，深受北方人喜爱，从此便有了一种新的茶叶品种茉莉花茶。"}]},{"name":"苏州茉莉花茶获奖情况","content":[{"text":"苏州素有“茉莉花城”之称，是全国驰名的茉莉花产地，从解放初期始，苏州茉莉\n        花茶\n        开始出口，外销香港、东南亚、欧洲、非洲等二十多个国家和地区。在国际上享有很高的荣誉 ，闻名海内外!"}]}]}